@import './variable.scss';

.header{
    color: $font-color;
    height: auto;
    display: flex;
    flex-direction: column;
    & .brand{
        margin: auto;
        margin-top: 20px;
        color: $font-color;
        display: flex;
        align-items: center;
        & .the{
            display: inline-block;
            font-size: 32px;
            -webkit-transform: rotate(-90deg);
            -moz-transform: rotate(-90deg);
            transform: rotate(-90deg);
        }
        & .siren{
            font-size: 71px;
            font-weight: 900;
        }
    }
    & .nav-bar{
        display: flex;
        justify-content: space-evenly;
        width: 100%;
        margin: auto;
        border-bottom: 2px solid $light-border;
        flex-grow: 1;
        & span{
            width: 15%;
            margin: 10px;
            padding: 10px;
            text-align: center;
            font-size: 18px;
        }
    }

}
